nav{
    padding:0 20px ;
}
.content{
    min-height: calc(100vh - 134px);
}
.list{
    display:grid;
    grid-template-columns:repeat(auto-fill,minmax(250px,1fr));
    gap:1rem;
}
img,iframe{
    max-width:100%;
}
iframe{
    width:600px;
    height:350px;
}
.recipe{
    margin-top:10px;
    margin-bottom:10px;
}